ul {
    box-sizing: border-box;
    width: 100%;
    border: 1px solid #ddd;
    border-radius: 5px;
    list-style-type: none;
    padding: 0 5px;
}

li {
    line-height: 30px;
    border-bottom: 1px dashed #ddd;
}

li:last-child {
    border-bottom: none;
}

li:hover {
    background-color: #ddd;
}

li button {
    margin-top: 5px;
    display: none;
    float: right;
}

li:hover button {
    display: block;
}